
.array-container {
    left: 10%;
    margin: auto;
    width: 80%;
    display: block;
    height: 100%;
    position: fixed;
}

.array-bar {
    width: 2px;
    display: inline-block;
    margin: 0 0.5px;
    bottom: 0;
}

select {
    background-color: white;
    color: #428bca;
    padding: 0px 20px;
    border-radius: 8px;
    margin: 5px;
}

option, label {
    color: #428bca;
    margin-left: 10px;
}
